<template>
  <div class="file-container">
    <div class="file-container__files">
      <div
        class="file-container__files--file"
        v-for="file in files"
        :key="file.name"
      >
        <v-icon name="file"></v-icon>
        <span>{{ file.name }}</span>
        <v-icon @click="removeFiles" class="remove" name="times"></v-icon>
      </div>
    </div>
    <FormulateInput
      class="document-uploader user-upload not-active achievement"
      label-class="document-label"
      name="file"
      type="file"
      validation="download"
      :validation-rules="{
        download: ({ value }) => {
          return files.length
        },
      }"
      :validation-messages="{
        download: 'Загрузите документ',
      }"
    >
      <template #element>
        <input
          @dragover="dragHover = true"
          @dragleave="dragHover = false"
          @dragend="dragHover = false"
          @drop="
            dragHover = false
            uploadFile()
          "
          @change="setFile"
          type="file"
          :multiple="multiple"
          id="upload"
          accept="application/pdf"
          :isDrag="dragHover"
          class="document-uploader--input"
        />
        <label name="file" for="upload" class="document-uploader--custom">
          <v-icon name="file"></v-icon>
          <span>{{ getLabel }}</span>
        </label>
      </template>
    </FormulateInput>
  </div>
</template>

<script>
export default {
  props: {
    multiple: {
      type: Boolean,
    },
  },
  data() {
    return {
      dragHover: false,
      uploadPercentage: 0,
      files: [],
    }
  },
  computed: {
    getLabel() {
      return this.dragHover ? 'Перетащите сюда' : 'Прикрепить файл'
    },
  },
  methods: {
    removeFiles() {
      this.files = []
    },
    async setFile(event) {
      this.files = event.target.files
      this.$emit('change', this.files[0])
    },
  },
}
</script>

